const LessonWrapper = ({ type }) => {
  const { index } = useParams();
  return <LessonContent type={type} index={parseInt(index)} />;
};

const BackToTopButton = () => {
  const [visible, setVisible] = useState(false);
  useEffect(() => {
    const toggleVisibility = () => setVisible(window.pageYOffset > 300);
    window.addEventListener("scroll", toggleVisibility);
    return () => window.removeEventListener("scroll", toggleVisibility);
  }, []);
  return visible ? (
    <button className="btn btn-warning position-fixed bottom-0 end-0 m-4 shadow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}>
      ↑ Top
    </button>
  ) : null;
};

