const App = () => {
  const [search, setSearch] = useState("");
  const navigate = useNavigate();

  const handleSearch = (e) => {
    e.preventDefault();
    const query = search.trim().toLowerCase();
  
    // Try matching in music lessons
    const musicIndex = lessons.music.findIndex(lesson =>
      lesson.title.toLowerCase().includes(query)
    );
    if (musicIndex !== -1) {
      navigate(`/music/${musicIndex}`);
      return;
    }
  
    // Try matching in dance lessons
    const danceIndex = lessons.dance.findIndex(lesson =>
      lesson.title.toLowerCase().includes(query)
    );
    if (danceIndex !== -1) {
      navigate(`/dance/${danceIndex}`);
      return;
    }
  
    // Fallback if nothing matched
    alert("No matching lesson found.");
  };
  

  return (
    <div className="background-blur">
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark shadow">
        <div className="container-fluid">
          <Link className="navbar-brand fw-bold text-warning" to="/">Sree Surabhi</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link text-white" to="/music">Music</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link text-white" to="/dance">Dance</Link>
              </li>
            </ul>
            <form className="d-flex" onSubmit={handleSearch}>
              <input className="form-control me-2" type="search" placeholder="Search Lessons" aria-label="Search" value={search} onChange={(e) => setSearch(e.target.value)} />
              <button className="btn btn-outline-warning" type="submit">Search</button>
            </form>
          </div>
        </div>
      </nav>

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/music" element={<LessonPage type="music" />} />
        <Route path="/dance" element={<LessonPage type="dance" />} />
        <Route path="/music/:index" element={<LessonWrapper type="music" />} />
        <Route path="/dance/:index" element={<LessonWrapper type="dance" />} />
      </Routes>

      <BackToTopButton />
    </div>
  );
};

export default App;
